import React, { useEffect, useState, useContext } from "react";
import { useHistory, useParams } from 'react-router-dom';
import { Helmet } from "react-helmet";
import { GlobalContext } from '../../../context/GlobalState';
import parse from 'html-react-parser';
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import { localRoute } from '../../../routes/localRoutes';

export default function BlogDetails1() {
    const { blogState, blogDispatch } = useContext(GlobalContext);
    const [blogDetail, setBlogDetail] = useState({});
    const [filterBlogDetails, setFilterBlogDetails] = useState([]);
    const [tabFilter, setTabFilter] = useState('active');
    const { blog_id } = useParams();
    const history = useHistory();

    console.log("blog_id 22",blog_id)
    useEffect(() => {
        window.scrollTo(0, 0);
        const fetchBlog = async () => {
            try {
                const response = await fetch(`https://api.monacoexperiences.com/api/v1/${blog_id}/`);
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }
                const data = await response.json();
                setBlogDetail(data);
            } catch (error) {
                console.error('Error fetching blog:', error);
                // Handle error state or logging here
            }
        };
        fetchBlog();
    }, [blog_id]);

    useEffect(() => {
        // Assuming you want to load recent blogs in the sidebar
        // You can fetch recent blogs here or use existing data in context
    }, [tabFilter]);

    const OpenBlog = (item) => {
        history.push(`${localRoute.blogDetails1}/${item.blog_slug}`);
    };

    return (
        <div>
            <Helmet>
                <meta charSet="utf-8" />
                <title>{blogDetail.blog_title && parse(blogDetail.blog_title)}</title>
                <meta name="description" content={blogDetail.meta_description} />
                <meta name="keywords" content={blogDetail.blog_meta_tag} />
                <link rel="canonical" href={`https://monacoexperiences.com${history.location.pathname}`} />
            </Helmet>
            <div className="mt-150 md-mt-90 font-montserrat page-title-area container" style={{ display: "flex", justifyContent: "space-between", padding: "0px 0px 0px" }}>
                <ul className="breadcrumb-nav" style={{ width: "245px" }}>
                    <li style={{ paddingRight: "10px" }}><a style={{ cursor: "pointer" }} onClick={() => history.push(`${localRoute.blog}`)}>Blog</a></li>
                    <li><ChevronRightIcon /> Blog Details</li>
                </ul>
                <ul className="breadcrumb-nav">
                    <li style={{ cursor: "pointer" }} onClick={() => history.push(`${localRoute.blog}`)}><KeyboardBackspaceIcon />Back</li>
                </ul>
            </div>
            <hr style={{ width: "80%", margin: "10px auto" }} />
            <section className="blog-area section-gap" style={{ paddingTop: "30px" }}>
                <div className="font-montserrat container">
                    <div className="row">
                        <div className="col-lg-8">
                            <div className="single-blog-post blog-details-content pr-xl-5">
                                <div className="post-thumbnail">
                                    <img src={blogDetail.blog_image} alt={blogDetail.blog_title} />
                                </div>
                                <br />
                                <div className="post-content">
                                    <h1 className="post-title">
                                        {blogDetail.blog_title && parse(blogDetail.blog_title)}
                                        <a className="font-montserrat fw-600">
                                        </a>
                                    </h1>
                                    <div style={{ listStyleType: "initial" }}>
                                        {blogDetail.blog_description && parse(blogDetail.blog_description)}
                                    </div>
                                    <blockquote className="font-montserrat fw-600">
                                        <p>Shoot an email to us at <span><u><a href="mailto:business@monacoexperiences.com">business@monacoexperiences.com</a></u></span> and book a 30-min call with our experts today! </p>
                                    </blockquote>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6">
                            <div className="blog-sidebar-one">
                                <div className="sidebar-recent-news mb-85">
                                    <h4 className="sidebar-title font-montserrat fw-600">Recent Blogs</h4>
                                    <ul>
                                        {filterBlogDetails.slice(0, 5).map(item =>
                                            <li key={item.blog_slug}>
                                                <a style={{ cursor: "pointer" }} onClick={() => OpenBlog(item)}>
                                                    <img src={item.blog_image} alt={item.blog_title} className="image-meta" />
                                                    <span className="font-montserrat title" style={{ display: "flex" }}>
                                                        {item.blog_title}
                                                    </span>
                                                </a>
                                            </li>
                                        )}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
